* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

:root {
  --fontetitulo: "Open Sans", sans-serif;
  --fonteparagrafo: "Quicksand", sans-serif;
}

body {
  background-image: linear-gradient(to bottom, #0091ff, #005da4);
  background-attachment: fixed;
}

.container {
  background-color: white;
  width: 570px;
  margin: auto;
  margin-top: 50px;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.3);
}

h1 {
  font: normal 800 1.5em var(--fontetitulo);
  margin-bottom: 10px;
}

input[type='text'] {
  border: none;
  padding: 1px 0px;
  outline: 1px solid #b4b4b4;
}

input[type='text']:focus-within {
  outline: 1px solid #0091ff;
}

input[type='submit'] {
  padding: 2px 10px;
  border: none;
  background-color: black;
  color: white;
  cursor: pointer;
  margin-top: 10px;
}

input[type='submit']:hover {
  background-color: #005da4;
  transition-duration: .2s;
}

input[type='submit']:active {
  background-color: #0091ff;
  transition-duration: 0s;
}

ul {
  list-style-position: inside;
  margin-top: 20px;
}

li {
  margin-bottom: 10px;
}

.senha-gerada {
  font-size: 2em;
  color: #005da4;
  margin: 15px 0px;
}

p {
  margin: 5px 0px;
}

input[type='checkbox'] {
  width: 20px;
  height: 20px;
}

.gerar-senha {
  background-color: #005da4;
  color: white;
  font-size: 1em;
  border: none;
  padding: 5px 15px;
  border-radius: 2px;
  margin-top: 5px;
  cursor: pointer;
}

.gerar-senha:hover {
  background-color: #0091ff;
  transition-duration: .2s;
}

.gerar-senha:active {
  background-color: white;
  color: #005da4;
  transition-duration: 0s;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAEhB;EACE,WAAW;EACX,YAAY;EACZ,sBAAsB;EACtB,mJAAmJ;AACrJ;;AAMA;EACE,sCAAsC;EACtC,yCAAyC;AAC3C;;AAEA;EACE,8DAA8D;EAC9D,4BAA4B;AAC9B;;AAEA;EACE,uBAAuB;EACvB,YAAY;EACZ,YAAY;EACZ,gBAAgB;EAChB,aAAa;EACb,kBAAkB;EAClB,0CAA0C;AAC5C;;AAEA;EACE,yCAAyC;EACzC,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,gBAAgB;EAChB,0BAA0B;AAC5B;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,iBAAiB;EACjB,YAAY;EACZ,uBAAuB;EACvB,YAAY;EACZ,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,yBAAyB;EACzB,wBAAwB;AAC1B;;AAEA;EACE,yBAAyB;EACzB,uBAAuB;AACzB;;AAEA;EACE,2BAA2B;EAC3B,gBAAgB;AAClB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,cAAc;EACd,gBAAgB;AAClB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,cAAc;EACd,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,yBAAyB;EACzB,wBAAwB;AAC1B;;AAEA;EACE,uBAAuB;EACvB,cAAc;EACd,uBAAuB;AACzB","sourcesContent":["@charset \"UTF-8\";\r\n\r\n* {\r\n  margin: 0px;\r\n  padding: 0px;\r\n  box-sizing: border-box;\r\n  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\r\n}\r\n\r\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');\r\n\r\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Quicksand:wght@300..700&display=swap');\r\n\r\n:root {\r\n  --fontetitulo: \"Open Sans\", sans-serif;\r\n  --fonteparagrafo: \"Quicksand\", sans-serif;\r\n}\r\n\r\nbody {\r\n  background-image: linear-gradient(to bottom, #0091ff, #005da4);\r\n  background-attachment: fixed;\r\n}\r\n\r\n.container {\r\n  background-color: white;\r\n  width: 570px;\r\n  margin: auto;\r\n  margin-top: 50px;\r\n  padding: 20px;\r\n  border-radius: 5px;\r\n  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.3);\r\n}\r\n\r\nh1 {\r\n  font: normal 800 1.5em var(--fontetitulo);\r\n  marg